import React from 'react'
//import { Animated } from 'react-native'
import styled from 'styled-components/native'
import { Ionicons } from '@expo/vector-icons'

import { mixin, countSize } from '../helpers'

const Base = styled.View`
  ${mixin}

  display: flex;
  align-items: center;
  justify-content: center;
`

const BorderMark = styled.View`
  ${mixin}

  ${({ color, size }: { color: string; size: number }) => `
    margin: ${countSize(size)}px;
    border-radius: ${(size - 2 * countSize(size)) / 2}px;
    border: ${countSize(size)}px solid ${color};
  `}
`

const FadeMark = styled.View`
  ${mixin}

  background-color: #fff8;

  ${({ size }: { size: number }) => `
    border-radius: ${size / 2}px;
  `}
`

export const Marker = ({ /*animate,*/ isDark, size, type }): JSX.Element => {
  let contrast: string = isDark ? '#fff' : '#000'

  return type === 'border' ? (
    <BorderMark color={contrast} size={size} />
  ) : type === 'checkmark' ? (
    <Base>
      <Ionicons color={contrast} name="md-checkmark" size={(size / 3) * 2} />
    </Base>
  ) : (
    type === 'fade' && <FadeMark size={size} />
  )
}
